"use client";

import { useState, useEffect } from "react";
import {
  Plus,
  Search,
  Filter,
  Edit,
  Trash2,
  Eye,
  Calendar,
  Users,
  Clock,
  Building2,
  ChevronDown,
  X,
  Star,
  MapPin,
  Briefcase,
} from "lucide-react";
import axios from "axios";

const CompanyListing = () => {
  const [activeTab, setActiveTab] = useState("current");
  const [searchTerm, setSearchTerm] = useState("");
  const [filterStatus, setFilterStatus] = useState("all");
  const [filterBatch, setFilterBatch] = useState("all");
  const [filterJobType, setFilterJobType] = useState("all");
  const [showAddModal, setShowAddModal] = useState(false);
  const [showFilters, setShowFilters] = useState(false);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  // State for data
  const [companies, setCompanies] = useState([]);
  const [batches, setBatches] = useState([]);
  const [selectedCompany, setSelectedCompany] = useState(null);

  // Form state
  const [newCompany, setNewCompany] = useState({
    company_name: "",
    company_description: "",
    company_type: "product",
    is_marquee: false,
    website_url: "",
    linkedin_url: "",
    primary_hr_name: "",
    primary_hr_designation: "",
    primary_hr_email: "",
    primary_hr_phone: "",
    scheduled_visit: "",
    actual_arrival: "",
    glassdoor_rating: "",
    work_locations: "",
    min_cgpa: "",
    max_backlogs: "",
    bond_required: false,
    batch_id: "",
    positions: [
      {
        position_title: "",
        job_type: "full_time",
        package_range: "",
      },
    ],
  });

  // Mock API functions - replace with your actual API calls
  const fetchCompanies = async () => {
    try {
      setLoading(true);
      const response = await axios.get("http://localhost:5000/companies");

      // Transform data to match your frontend structure
      const transformedData = response.data.map((company) => ({
        ...company,
        status: getCompanyStatus(company),
        type: company.scheduled_visit > new Date() ? "upcoming" : "current",
        applied: company.applications_count || 0,
      }));

      setCompanies(transformedData);
    } catch (error) {
      setError("Failed to fetch companies");
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  const fetchBatches = async () => {
    try {
      const response = await axios.get("http://localhost:5000/batches");
      setBatches(response.data);
    } catch (error) {
      console.error("Failed to fetch batches:", error);
    }
  };

  const getCompanyStatus = (company) => {
    const today = new Date();
    const scheduledDate = new Date(company.scheduled_visit);
    const arrivalDate = company.actual_arrival
      ? new Date(company.actual_arrival)
      : null;

    if (arrivalDate && arrivalDate < today) {
      return "completed";
    } else if (
      scheduledDate <= today &&
      (!arrivalDate || arrivalDate >= today)
    ) {
      return "active";
    } else if (scheduledDate > today) {
      return "upcoming";
    }
    return "pipeline";
  };

  useEffect(() => {
    fetchCompanies();
    fetchBatches();
  }, []);

  const statusColors = {
    active: "bg-green-100 text-green-800",
    upcoming: "bg-blue-100 text-blue-800",
    completed: "bg-gray-100 text-gray-800",
    pipeline: "bg-yellow-100 text-yellow-800",
    cancelled: "bg-red-100 text-red-800",
  };

  const jobTypeColors = {
    internship: "bg-purple-100 text-purple-800",
    full_time: "bg-green-100 text-green-800",
    internship_plus_ppo: "bg-blue-100 text-blue-800",
  };

  const companyTypeColors = {
    product: "bg-blue-100 text-blue-800",
    service: "bg-orange-100 text-orange-800",
    startup: "bg-green-100 text-green-800",
    consulting: "bg-purple-100 text-purple-800",
  };

  const filteredCompanies = companies.filter((company) => {
    const matchesTab =
      activeTab === "all" ||
      (activeTab === "current" && company.type === "current") ||
      (activeTab === "upcoming" && company.type === "upcoming");

    const matchesSearch = company.company_name
      .toLowerCase()
      .includes(searchTerm.toLowerCase());

    const matchesFilter =
      filterStatus === "all" || company.status === filterStatus;

    const matchesBatch =
      filterBatch === "all" || company.batch_id?.toString() === filterBatch;

    const matchesJobType =
      filterJobType === "all" ||
      company.positions?.some((pos) => pos.job_type === filterJobType);

    return (
      matchesTab &&
      matchesSearch &&
      matchesFilter &&
      matchesBatch &&
      matchesJobType
    );
  });

  const handleAddPosition = () => {
    setNewCompany({
      ...newCompany,
      positions: [
        ...newCompany.positions,
        {
          position_title: "",
          job_type: "full_time",
          package_range: "",
        },
      ],
    });
  };

  const handleRemovePosition = (index) => {
    const updatedPositions = newCompany.positions.filter((_, i) => i !== index);
    setNewCompany({ ...newCompany, positions: updatedPositions });
  };

  const handlePositionChange = (index, field, value) => {
    const updatedPositions = newCompany.positions.map((pos, i) =>
      i === index ? { ...pos, [field]: value } : pos
    );
    setNewCompany({ ...newCompany, positions: updatedPositions });
  };

  const handleAddCompany = async () => {
    try {
      if (
        !newCompany.company_name ||
        !newCompany.scheduled_visit ||
        !newCompany.batch_id
      ) {
        alert("Please fill in required fields");
        return;
      }

      // Prepare data for API
      const companyData = {
        ...newCompany,
        glassdoor_rating: parseFloat(newCompany.glassdoor_rating) || null,
        min_cgpa: parseFloat(newCompany.min_cgpa) || 0.0,
        max_backlogs: parseInt(newCompany.max_backlogs) || 999,
        package_range: parseFloat(newCompany.package_range) || null,
      };

      const response = await axios.post(
        "http://localhost:5000/companies",
        companyData
      );

      if (response.status === 200) {
        await fetchCompanies(); // Refresh the list
        setShowAddModal(false);
        resetForm();
      }
    } catch (error) {
      setError("Failed to add company");
      console.error(error);
    }
  };

  const resetForm = () => {
    setNewCompany({
      company_name: "",
      company_description: "",
      company_type: "product",
      is_marquee: false,
      website_url: "",
      linkedin_url: "",
      primary_hr_name: "",
      primary_hr_designation: "",
      primary_hr_email: "",
      primary_hr_phone: "",
      scheduled_visit: "",
      actual_arrival: "",
      glassdoor_rating: "",
      work_locations: "",
      min_cgpa: "",
      max_backlogs: "",
      bond_required: false,
      batch_id: "",
      positions: [
        {
          position_title: "",
          job_type: "full_time",
          package_range: "",
        },
      ],
    });
  };

  const handleDeleteCompany = async (companyId) => {
    if (window.confirm("Are you sure you want to delete this company?")) {
      try {
        const response = await axios.delete(
          `http://localhost:5000/companies/${companyId}`
        );

        if (response.status === 200) {
          await fetchCompanies(); // Refresh the list
        }
      } catch (error) {
        setError("Failed to delete company");
        console.error(error);
      }
    }
  };

  const getStats = () => {
    const total = companies.length;
    const active = companies.filter((c) => c.status === "active").length;
    const upcoming = companies.filter(
      (c) => c.status === "upcoming" || c.status === "pipeline"
    ).length;
    const completed = companies.filter((c) => c.status === "completed").length;
    const marquee = companies.filter((c) => c.is_marquee).length;

    return { total, active, upcoming, completed, marquee };
  };

  const stats = getStats();

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto"></div>
          <p className="mt-4 text-gray-600">Loading companies...</p>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <div className="text-red-600 text-lg font-medium">{error}</div>
          <button
            onClick={() => window.location.reload()}
            className="mt-4 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700"
          >
            Retry
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50 p-6">
      <div className="max-w-7xl mx-auto">
        {/* Header */}
        <div className="bg-white rounded-lg shadow-sm p-6 mb-6">
          <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between gap-4">
            <div>
              <h1 className="text-2xl font-bold text-gray-900">
                Company Management
              </h1>
              <p className="text-gray-600 mt-1">
                Manage current and upcoming recruitment drives across batches
              </p>
            </div>
            <button
              onClick={() => setShowAddModal(true)}
              className="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded-lg flex items-center gap-2 transition-colors"
            >
              <Plus size={20} />
              Add Company
            </button>
          </div>

          {/* Stats Cards */}
          <div className="grid grid-cols-2 lg:grid-cols-5 gap-4 mt-6">
            <div className="bg-blue-50 p-4 rounded-lg">
              <div className="flex items-center gap-3">
                <Building2 className="text-blue-600" size={24} />
                <div>
                  <p className="text-blue-600 text-sm font-medium">
                    Total Companies
                  </p>
                  <p className="text-2xl font-bold text-blue-900">
                    {stats.total}
                  </p>
                </div>
              </div>
            </div>
            <div className="bg-green-50 p-4 rounded-lg">
              <div className="flex items-center gap-3">
                <Users className="text-green-600" size={24} />
                <div>
                  <p className="text-green-600 text-sm font-medium">Active</p>
                  <p className="text-2xl font-bold text-green-900">
                    {stats.active}
                  </p>
                </div>
              </div>
            </div>
            <div className="bg-yellow-50 p-4 rounded-lg">
              <div className="flex items-center gap-3">
                <Clock className="text-yellow-600" size={24} />
                <div>
                  <p className="text-yellow-600 text-sm font-medium">
                    Upcoming
                  </p>
                  <p className="text-2xl font-bold text-yellow-900">
                    {stats.upcoming}
                  </p>
                </div>
              </div>
            </div>
            <div className="bg-gray-50 p-4 rounded-lg">
              <div className="flex items-center gap-3">
                <Calendar className="text-gray-600" size={24} />
                <div>
                  <p className="text-gray-600 text-sm font-medium">Completed</p>
                  <p className="text-2xl font-bold text-gray-900">
                    {stats.completed}
                  </p>
                </div>
              </div>
            </div>
            <div className="bg-purple-50 p-4 rounded-lg">
              <div className="flex items-center gap-3">
                <Star className="text-purple-600" size={24} />
                <div>
                  <p className="text-purple-600 text-sm font-medium">Marquee</p>
                  <p className="text-2xl font-bold text-purple-900">
                    {stats.marquee}
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Filters and Search */}
        <div className="bg-white rounded-lg shadow-sm p-6 mb-6">
          {/* Tabs */}
          <div className="flex flex-wrap gap-2 mb-4">
            {["current", "upcoming", "all"].map((tab) => (
              <button
                key={tab}
                onClick={() => setActiveTab(tab)}
                className={`px-4 py-2 rounded-lg font-medium transition-colors ${
                  activeTab === tab
                    ? "bg-blue-600 text-white"
                    : "bg-gray-100 text-gray-700 hover:bg-gray-200"
                }`}
              >
                {tab.charAt(0).toUpperCase() + tab.slice(1)} Companies
              </button>
            ))}
          </div>

          {/* Search and Filters */}
          <div className="flex flex-col lg:flex-row gap-4">
            <div className="flex-1 relative">
              <Search
                className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400"
                size={20}
              />
              <input
                type="text"
                placeholder="Search companies..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
              />
            </div>
            <div className="flex gap-2">
              <button
                onClick={() => setShowFilters(!showFilters)}
                className="px-4 py-2 border border-gray-300 rounded-lg hover:bg-gray-50 flex items-center gap-2"
              >
                <Filter size={20} />
                Filters
                <ChevronDown size={16} />
              </button>
            </div>
          </div>

          {/* Advanced Filters */}
          {showFilters && (
            <div className="mt-4 p-4 bg-gray-50 rounded-lg">
              <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Status
                  </label>
                  <select
                    value={filterStatus}
                    onChange={(e) => setFilterStatus(e.target.value)}
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                  >
                    <option value="all">All Status</option>
                    <option value="active">Active</option>
                    <option value="upcoming">Upcoming</option>
                    <option value="completed">Completed</option>
                    <option value="pipeline">Pipeline</option>
                  </select>
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Batch
                  </label>
                  <select
                    value={filterBatch}
                    onChange={(e) => setFilterBatch(e.target.value)}
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                  >
                    <option value="all">All Batches</option>
                    {batches.map((batch) => (
                      <option key={batch.id} value={batch.id}>
                        {batch.name}
                      </option>
                    ))}
                  </select>
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Job Type
                  </label>
                  <select
                    value={filterJobType}
                    onChange={(e) => setFilterJobType(e.target.value)}
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                  >
                    <option value="all">All Job Types</option>
                    <option value="full_time">Full Time</option>
                    <option value="internship">Internship</option>
                    <option value="internship_plus_ppo">
                      Internship + PPO
                    </option>
                  </select>
                </div>
                <div className="flex items-end">
                  <button
                    onClick={() => {
                      setFilterStatus("all");
                      setFilterBatch("all");
                      setFilterJobType("all");
                      setSearchTerm("");
                    }}
                    className="w-full px-4 py-2 bg-gray-200 text-gray-700 rounded-lg hover:bg-gray-300"
                  >
                    Clear Filters
                  </button>
                </div>
              </div>
            </div>
          )}
        </div>

        {/* Companies List */}
        <div className="bg-white rounded-lg shadow-sm overflow-hidden">
          <div className="overflow-x-auto">
            <table className="w-full">
              <thead className="bg-gray-50">
                <tr>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Company
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Status
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Type
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Positions
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Batch
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Rating
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Scheduled Visit
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Actual Arrival
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    CGPA Required
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Applied
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Actions
                  </th>
                </tr>
              </thead>
              <tbody className="bg-white divide-y divide-gray-200">
                {filteredCompanies.map((company) => (
                  <tr key={company.id} className="hover:bg-gray-50">
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="flex items-center">
                        <div>
                          <div className="flex items-center gap-2">
                            <div className="text-sm font-medium text-gray-900">
                              {company.company_name}
                            </div>
                            {company.is_marquee && (
                              <Star className="text-yellow-500" size={16} />
                            )}
                          </div>
                          <div className="text-sm text-gray-500">
                            {company.company_description}
                          </div>
                          {company.work_locations && (
                            <div className="flex items-center gap-1 mt-1">
                              <MapPin className="text-gray-400" size={12} />
                              <span className="text-xs text-gray-500">
                                {company.work_locations}
                              </span>
                            </div>
                          )}
                        </div>
                      </div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <span
                        className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${statusColors[company.status]}`}
                      >
                        {company.status}
                      </span>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <span
                        className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${companyTypeColors[company.company_type]}`}
                      >
                        {company.company_type}
                      </span>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                      <div className="flex flex-wrap gap-1">
                        {company.positions?.map((position, index) => (
                          <div key={index} className="mb-1">
                            <div className="text-xs font-medium text-gray-900">
                              {position.position_title}
                            </div>
                            <div className="flex gap-1">
                              <span
                                className={`inline-flex px-1 py-0.5 text-xs font-medium rounded ${jobTypeColors[position.job_type]}`}
                              >
                                {position.job_type.replace("_", " ")}
                              </span>
                              {position.package_range && (
                                <span className="text-xs text-gray-600">
                                  â‚¹{position.package_range}L
                                </span>
                              )}
                            </div>
                          </div>
                        ))}
                      </div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                      {batches.find((b) => b.id === company.batch_id)?.name ||
                        "N/A"}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      {company.glassdoor_rating && (
                        <div className="flex items-center gap-1">
                          <span
                            className={`text-sm font-medium ${
                              company.glassdoor_rating >= 4
                                ? "text-green-600"
                                : company.glassdoor_rating >= 3
                                  ? "text-yellow-600"
                                  : "text-red-600"
                            }`}
                          >
                            {company.glassdoor_rating}
                          </span>
                          <svg
                            className="w-4 h-4 text-yellow-400 fill-current"
                            viewBox="0 0 20 20"
                          >
                            <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
                          </svg>
                        </div>
                      )}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                      <div className="flex items-center gap-1">
                        <Calendar size={16} className="text-gray-400" />
                        {new Date(company.scheduled_visit).toLocaleDateString()}
                      </div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                      <div className="flex items-center gap-1">
                        <Calendar size={16} className="text-gray-400" />
                        {company.actual_arrival
                          ? new Date(
                              company.actual_arrival
                            ).toLocaleDateString()
                          : "-"}
                      </div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                      <div className="text-sm">
                        <div>CGPA: {company.min_cgpa || "Any"}</div>
                        <div className="text-xs text-gray-500">
                          Max Backlogs:{" "}
                          {company.max_backlogs === 999
                            ? "Any"
                            : company.max_backlogs}
                        </div>
                        {company.bond_required && (
                          <div className="text-xs text-red-600">
                            Bond Required
                          </div>
                        )}
                      </div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                      <span className="font-medium">
                        {company.applied || 0}
                      </span>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                      <div className="flex items-center gap-2">
                        <button
                          className="text-blue-600 hover:text-blue-900"
                          onClick={() => setSelectedCompany(company)}
                        >
                          <Eye size={16} />
                        </button>
                        <button className="text-gray-600 hover:text-gray-900">
                          <Edit size={16} />
                        </button>
                        <button
                          className="text-red-600 hover:text-red-900"
                          onClick={() => handleDeleteCompany(company.id)}
                        >
                          <Trash2 size={16} />
                        </button>
                      </div>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>

          {filteredCompanies.length === 0 && (
            <div className="text-center py-12">
              <Building2 className="mx-auto h-12 w-12 text-gray-400" />
              <h3 className="mt-2 text-sm font-semibold text-gray-900">
                No companies found
              </h3>
              <p className="mt-1 text-sm text-gray-500">
                Try adjusting your search or filters
              </p>
            </div>
          )}
        </div>

        {/* Add Company Modal */}
        {showAddModal && (
          <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
            <div className="bg-white rounded-lg p-6 w-full max-w-4xl max-h-[90vh] overflow-y-auto">
              <div className="flex justify-between items-center mb-4">
                <h2 className="text-xl font-bold text-gray-900">
                  Add New Company
                </h2>
                <button
                  onClick={() => setShowAddModal(false)}
                  className="text-gray-400 hover:text-gray-600"
                >
                  <X size={24} />
                </button>
              </div>

              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                {/* Company Basic Info */}
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Company Name *
                  </label>
                  <input
                    type="text"
                    value={newCompany.company_name}
                    onChange={(e) =>
                      setNewCompany({
                        ...newCompany,
                        company_name: e.target.value,
                      })
                    }
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                    placeholder="e.g., Google India"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Company Type *
                  </label>
                  <select
                    value={newCompany.company_type}
                    onChange={(e) =>
                      setNewCompany({
                        ...newCompany,
                        company_type: e.target.value,
                      })
                    }
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                  >
                    <option value="product">Product</option>
                    <option value="service">Service</option>
                    <option value="startup">Startup</option>
                    <option value="consulting">Consulting</option>
                  </select>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Batch *
                  </label>
                  <select
                    value={newCompany.batch_id}
                    onChange={(e) =>
                      setNewCompany({ ...newCompany, batch_id: e.target.value })
                    }
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                  >
                    <option value="">Select Batch</option>
                    {batches.map((batch) => (
                      <option key={batch.id} value={batch.id}>
                        {batch.name}
                      </option>
                    ))}
                  </select>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Scheduled Visit Date *
                  </label>
                  <input
                    type="date"
                    value={newCompany.scheduled_visit}
                    onChange={(e) =>
                      setNewCompany({
                        ...newCompany,
                        scheduled_visit: e.target.value,
                      })
                    }
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Actual Arrival Date
                  </label>
                  <input
                    type="date"
                    value={newCompany.actual_arrival}
                    onChange={(e) =>
                      setNewCompany({
                        ...newCompany,
                        actual_arrival: e.target.value,
                      })
                    }
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Work Locations
                  </label>
                  <input
                    type="text"
                    value={newCompany.work_locations}
                    onChange={(e) =>
                      setNewCompany({
                        ...newCompany,
                        work_locations: e.target.value,
                      })
                    }
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                    placeholder="e.g., Bangalore, Chennai, Hyderabad"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Glassdoor Rating
                  </label>
                  <input
                    type="number"
                    min="0"
                    max="5"
                    step="0.1"
                    value={newCompany.glassdoor_rating}
                    onChange={(e) =>
                      setNewCompany({
                        ...newCompany,
                        glassdoor_rating: e.target.value,
                      })
                    }
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                    placeholder="e.g., 4.5"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Minimum CGPA
                  </label>
                  <input
                    type="number"
                    min="0"
                    max="10"
                    step="0.1"
                    value={newCompany.min_cgpa}
                    onChange={(e) =>
                      setNewCompany({ ...newCompany, min_cgpa: e.target.value })
                    }
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                    placeholder="e.g., 7.5"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Maximum Backlogs
                  </label>
                  <input
                    type="number"
                    min="0"
                    value={newCompany.max_backlogs}
                    onChange={(e) =>
                      setNewCompany({
                        ...newCompany,
                        max_backlogs: e.target.value,
                      })
                    }
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                    placeholder="e.g., 2 (leave empty for no limit)"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Website URL
                  </label>
                  <input
                    type="url"
                    value={newCompany.website_url}
                    onChange={(e) =>
                      setNewCompany({
                        ...newCompany,
                        website_url: e.target.value,
                      })
                    }
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                    placeholder="https://company.com"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    LinkedIn URL
                  </label>
                  <input
                    type="url"
                    value={newCompany.linkedin_url}
                    onChange={(e) =>
                      setNewCompany({
                        ...newCompany,
                        linkedin_url: e.target.value,
                      })
                    }
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                    placeholder="https://linkedin.com/company/..."
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Primary HR Name
                  </label>
                  <input
                    type="text"
                    value={newCompany.primary_hr_name}
                    onChange={(e) =>
                      setNewCompany({
                        ...newCompany,
                        primary_hr_name: e.target.value,
                      })
                    }
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                    placeholder="HR Contact Name"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    HR Designation
                  </label>
                  <input
                    type="text"
                    value={newCompany.primary_hr_designation}
                    onChange={(e) =>
                      setNewCompany({
                        ...newCompany,
                        primary_hr_designation: e.target.value,
                      })
                    }
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                    placeholder="e.g., Senior HR Manager"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    HR Email
                  </label>
                  <input
                    type="email"
                    value={newCompany.primary_hr_email}
                    onChange={(e) =>
                      setNewCompany({
                        ...newCompany,
                        primary_hr_email: e.target.value,
                      })
                    }
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                    placeholder="hr@company.com"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    HR Phone
                  </label>
                  <input
                    type="tel"
                    value={newCompany.primary_hr_phone}
                    onChange={(e) =>
                      setNewCompany({
                        ...newCompany,
                        primary_hr_phone: e.target.value,
                      })
                    }
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                    placeholder="+91 9876543210"
                  />
                </div>

                <div className="md:col-span-2">
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Company Description
                  </label>
                  <textarea
                    value={newCompany.company_description}
                    onChange={(e) =>
                      setNewCompany({
                        ...newCompany,
                        company_description: e.target.value,
                      })
                    }
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                    rows="3"
                    placeholder="Brief description of the company and its work culture"
                  />
                </div>

                <div className="md:col-span-2 flex items-center gap-4">
                  <label className="flex items-center gap-2">
                    <input
                      type="checkbox"
                      checked={newCompany.is_marquee}
                      onChange={(e) =>
                        setNewCompany({
                          ...newCompany,
                          is_marquee: e.target.checked,
                        })
                      }
                      className="rounded border-gray-300 text-blue-600 focus:ring-blue-500"
                    />
                    <span className="text-sm text-gray-700">
                      Marquee Company
                    </span>
                  </label>

                  <label className="flex items-center gap-2">
                    <input
                      type="checkbox"
                      checked={newCompany.bond_required}
                      onChange={(e) =>
                        setNewCompany({
                          ...newCompany,
                          bond_required: e.target.checked,
                        })
                      }
                      className="rounded border-gray-300 text-blue-600 focus:ring-blue-500"
                    />
                    <span className="text-sm text-gray-700">Bond Required</span>
                  </label>
                </div>
              </div>

              {/* Positions Section */}
              <div className="mt-6 border-t pt-6">
                <div className="flex justify-between items-center mb-4">
                  <h3 className="text-lg font-medium text-gray-900">
                    Positions
                  </h3>
                  <button
                    type="button"
                    onClick={handleAddPosition}
                    className="bg-blue-600 hover:bg-blue-700 text-white px-3 py-1 rounded text-sm flex items-center gap-1"
                  >
                    <Plus size={16} />
                    Add Position
                  </button>
                </div>

                {newCompany.positions.map((position, index) => (
                  <div
                    key={index}
                    className="border border-gray-200 rounded-lg p-4 mb-4"
                  >
                    <div className="flex justify-between items-center mb-3">
                      <h4 className="text-sm font-medium text-gray-700">
                        Position {index + 1}
                      </h4>
                      {newCompany.positions.length > 1 && (
                        <button
                          type="button"
                          onClick={() => handleRemovePosition(index)}
                          className="text-red-600 hover:text-red-800"
                        >
                          <X size={16} />
                        </button>
                      )}
                    </div>

                    <div className="grid grid-cols-1 md:grid-cols-3 gap-3">
                      <div>
                        <label className="block text-xs font-medium text-gray-700 mb-1">
                          Position Title *
                        </label>
                        <input
                          type="text"
                          value={position.position_title}
                          onChange={(e) =>
                            handlePositionChange(
                              index,
                              "position_title",
                              e.target.value
                            )
                          }
                          className="w-full px-2 py-1 border border-gray-300 rounded text-sm focus:ring-2 focus:ring-blue-500"
                          placeholder="e.g., Software Engineer"
                        />
                      </div>

                      <div>
                        <label className="block text-xs font-medium text-gray-700 mb-1">
                          Job Type
                        </label>
                        <select
                          value={position.job_type}
                          onChange={(e) =>
                            handlePositionChange(
                              index,
                              "job_type",
                              e.target.value
                            )
                          }
                          className="w-full px-2 py-1 border border-gray-300 rounded text-sm focus:ring-2 focus:ring-blue-500"
                        >
                          <option value="full_time">Full Time</option>
                          <option value="internship">Internship</option>
                          <option value="internship_plus_ppo">
                            Internship + PPO
                          </option>
                        </select>
                      </div>

                      <div>
                        <label className="block text-xs font-medium text-gray-700 mb-1">
                          Package (LPA)
                        </label>
                        <input
                          type="number"
                          step="0.1"
                          value={position.package_range}
                          onChange={(e) =>
                            handlePositionChange(
                              index,
                              "package_range",
                              e.target.value
                            )
                          }
                          className="w-full px-2 py-1 border border-gray-300 rounded text-sm focus:ring-2 focus:ring-blue-500"
                          placeholder="e.g., 12.5"
                        />
                      </div>
                    </div>
                  </div>
                ))}
              </div>

              <div className="flex justify-end gap-3 mt-6">
                <button
                  onClick={() => {
                    setShowAddModal(false);
                    resetForm();
                  }}
                  className="px-4 py-2 text-gray-700 border border-gray-300 rounded-lg hover:bg-gray-50"
                >
                  Cancel
                </button>
                <button
                  onClick={handleAddCompany}
                  className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700"
                >
                  Add Company
                </button>
              </div>
            </div>
          </div>
        )}

        {/* Company Details Modal */}
        {selectedCompany && (
          <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
            <div className="bg-white rounded-lg p-6 w-full max-w-4xl max-h-[90vh] overflow-y-auto">
              <div className="flex justify-between items-center mb-4">
                <div className="flex items-center gap-3">
                  <h2 className="text-xl font-bold text-gray-900">
                    {selectedCompany.company_name}
                  </h2>
                  {selectedCompany.is_marquee && (
                    <Star className="text-yellow-500" size={20} />
                  )}
                  <span
                    className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${companyTypeColors[selectedCompany.company_type]}`}
                  >
                    {selectedCompany.company_type}
                  </span>
                </div>
                <button
                  onClick={() => setSelectedCompany(null)}
                  className="text-gray-400 hover:text-gray-600"
                >
                  <X size={24} />
                </button>
              </div>

              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                {/* Company Info */}
                <div className="space-y-4">
                  <div>
                    <h3 className="text-lg font-medium text-gray-900 mb-3">
                      Company Information
                    </h3>

                    {selectedCompany.company_description && (
                      <div className="mb-4">
                        <p className="text-sm text-gray-600">
                          {selectedCompany.company_description}
                        </p>
                      </div>
                    )}

                    <div className="space-y-2 text-sm">
                      {selectedCompany.website_url && (
                        <div>
                          <span className="font-medium">Website: </span>
                          <a
                            href={selectedCompany.website_url}
                            target="_blank"
                            rel="noopener noreferrer"
                            className="text-blue-600 hover:underline"
                          >
                            {selectedCompany.website_url}
                          </a>
                        </div>
                      )}

                      {selectedCompany.linkedin_url && (
                        <div>
                          <span className="font-medium">LinkedIn: </span>
                          <a
                            href={selectedCompany.linkedin_url}
                            target="_blank"
                            rel="noopener noreferrer"
                            className="text-blue-600 hover:underline"
                          >
                            {selectedCompany.linkedin_url}
                          </a>
                        </div>
                      )}

                      {selectedCompany.work_locations && (
                        <div>
                          <span className="font-medium">Locations: </span>
                          <span>{selectedCompany.work_locations}</span>
                        </div>
                      )}

                      {selectedCompany.glassdoor_rating && (
                        <div>
                          <span className="font-medium">
                            Glassdoor Rating:{" "}
                          </span>
                          <span className="flex items-center gap-1">
                            {selectedCompany.glassdoor_rating}
                            <Star
                              className="text-yellow-400 fill-current"
                              size={16}
                            />
                          </span>
                        </div>
                      )}
                    </div>
                  </div>

                  {/* HR Contact */}
                  {(selectedCompany.primary_hr_name ||
                    selectedCompany.primary_hr_email) && (
                    <div>
                      <h4 className="font-medium text-gray-900 mb-2">
                        HR Contact
                      </h4>
                      <div className="space-y-1 text-sm">
                        {selectedCompany.primary_hr_name && (
                          <div>
                            <span className="font-medium">Name: </span>
                            <span>{selectedCompany.primary_hr_name}</span>
                          </div>
                        )}
                        {selectedCompany.primary_hr_designation && (
                          <div>
                            <span className="font-medium">Designation: </span>
                            <span>
                              {selectedCompany.primary_hr_designation}
                            </span>
                          </div>
                        )}
                        {selectedCompany.primary_hr_email && (
                          <div>
                            <span className="font-medium">Email: </span>
                            <a
                              href={`mailto:${selectedCompany.primary_hr_email}`}
                              className="text-blue-600 hover:underline"
                            >
                              {selectedCompany.primary_hr_email}
                            </a>
                          </div>
                        )}
                        {selectedCompany.primary_hr_phone && (
                          <div>
                            <span className="font-medium">Phone: </span>
                            <span>{selectedCompany.primary_hr_phone}</span>
                          </div>
                        )}
                      </div>
                    </div>
                  )}
                </div>

                {/* Requirements and Schedule */}
                <div className="space-y-4">
                  <div>
                    <h4 className="font-medium text-gray-900 mb-2">
                      Requirements
                    </h4>
                    <div className="space-y-2 text-sm">
                      <div>
                        <span className="font-medium">Min CGPA: </span>
                        <span>{selectedCompany.min_cgpa || "Any"}</span>
                      </div>
                      <div>
                        <span className="font-medium">Max Backlogs: </span>
                        <span>
                          {selectedCompany.max_backlogs === 999
                            ? "Any"
                            : selectedCompany.max_backlogs}
                        </span>
                      </div>
                      {selectedCompany.bond_required && (
                        <div className="text-red-600 font-medium">
                          Bond Required
                        </div>
                      )}
                    </div>
                  </div>

                  <div>
                    <h4 className="font-medium text-gray-900 mb-2">Schedule</h4>
                    <div className="space-y-2 text-sm">
                      <div>
                        <span className="font-medium">Scheduled Visit: </span>
                        <span>
                          {new Date(
                            selectedCompany.scheduled_visit
                          ).toLocaleDateString()}
                        </span>
                      </div>
                      {selectedCompany.actual_arrival && (
                        <div>
                          <span className="font-medium">Actual Arrival: </span>
                          <span>
                            {new Date(
                              selectedCompany.actual_arrival
                            ).toLocaleDateString()}
                          </span>
                        </div>
                      )}
                      <div>
                        <span className="font-medium">Status: </span>
                        <span
                          className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${statusColors[selectedCompany.status]}`}
                        >
                          {selectedCompany.status}
                        </span>
                      </div>
                    </div>
                  </div>

                  <div>
                    <h4 className="font-medium text-gray-900 mb-2">Batch</h4>
                    <span className="text-sm">
                      {batches.find((b) => b.id === selectedCompany.batch_id)
                        ?.name || "N/A"}
                    </span>
                  </div>
                </div>
              </div>

              {/* Positions */}
              {selectedCompany.positions &&
                selectedCompany.positions.length > 0 && (
                  <div className="mt-6 border-t pt-6">
                    <h4 className="font-medium text-gray-900 mb-3">
                      Available Positions
                    </h4>
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                      {selectedCompany.positions.map((position, index) => (
                        <div
                          key={index}
                          className="border border-gray-200 rounded-lg p-4"
                        >
                          <div className="flex justify-between items-start mb-2">
                            <h5 className="font-medium text-gray-900">
                              {position.position_title}
                            </h5>
                            <span
                              className={`inline-flex px-2 py-1 text-xs font-medium rounded ${jobTypeColors[position.job_type]}`}
                            >
                              {position.job_type.replace("_", " ")}
                            </span>
                          </div>
                          {position.package_range && (
                            <p className="text-sm text-gray-600">
                              Package: â‚¹{position.package_range} LPA
                            </p>
                          )}
                        </div>
                      ))}
                    </div>
                  </div>
                )}
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default CompanyListing;
